import React from 'react';
import _ from 'lodash'
import { getTime, getUserFullName, getFirstWordOfName } from '../../../helpers/UtilsHelper';
import './CustomEvent.scss'

export default function CustomEvent(props) {
    const booking = props.event.extendedProps
    return (
        <>
            {
                booking && <div className="cursor-pointer admin-custom-event">
                    <div className="pl-2">
                        <div className="pt-1 font-weight-bold admin-custom-event__title">{getUserFullName(booking.provider)}</div>
                        <small className="mt-2">{getTime(`${props.event.start}`)} - {getTime(`${props.event.end}`)}</small>
                        {props.children}
                    </div>
                    <AvatarDisplay {...booking.provider} />
                </div>
            }
        </>
    );
}

const AvatarDisplay = (provider) => {
    const sortName = getFirstWordOfName(provider)
    if (!_.isEmpty(provider.avatar)) {
        return (
            <div>
                <img
                    alt="..."
                    className="avatar avatar-sm rounded-circle mr-2 admin-custom-event__pic-avatar"
                    src={provider.avatar}
                />
            </div>
        )
    }
    else {
        return (
            <div className="admin-custom-event__name-avatar">{sortName}</div>
        )
    }
}