import React from "react";
import { connect } from "react-redux";
import Select2 from 'react-select2-wrapper'
import {
    Button,
    Card,
} from "reactstrap";

import { settingCalendarUIWithDayView } from '../../../helpers/UtilsHelper'
import SectionHeader from '../../../components/Form/SectionHeader'

import { changeVenue, changeSpaces } from '../../../redux/actions/adminDashboardActions'
import constants from "../../../constants";

function VenueAndSpaceSelection(props) {

    const {
        //state
        venues,
        spaces,
        selectedVenue,
        selectedSpaces,
        calendarViewType,

        //actions
        changeVenue,
        changeSpaces,
    } = props

    const locationData = venues.map(venue => ({ ...venue, text: venue.name }));
    return <Card className="spaces">
        <div>
            <SectionHeader icon="far fa-building" title="Venue" />
            <div className="mb-3">
                <Select2
                    className="form-control"
                    defaultValue={selectedVenue}
                    onChange={state => changeVenue(state.target.value, settingCalendarUIWithDayView)}
                    options={{
                        placeholder: "Select venue",
                        templateResult: state => state.name,
                        templateSelection: state => state.name,
                        data: locationData
                    }}
                />
            </div>
        </div>
        <div>
            <SectionHeader icon="ni ni-ui-04" title="Spaces" />
            <div className="spaces-select">
                <SpaceSelection
                    spaces={spaces}
                    selectedSpaces={selectedSpaces}
                    onChange={changeSpaces}
                    calendarViewType={calendarViewType}
                />
            </div>
        </div>
    </Card>
}

const mapStateToProps = state => {
    const {
        venues,
        spaces,
        selectedVenue,
        selectedSpaces,
        calendarViewType
    } = state.adminDashboard

    return {
        venues,
        spaces,
        selectedVenue,
        selectedSpaces,
        calendarViewType
    }
}

const mapDispatchToProps = { changeVenue, changeSpaces }

export default connect(mapStateToProps, mapDispatchToProps)(React.memo(VenueAndSpaceSelection));

function SpaceSelection(props) {
    const { spaces, onChange, calendarViewType, selectedSpaces } = props;

    let isAllowClick = true;
    let selected;

    if (calendarViewType === constants.CALENDAR_VIEW_TYPE.DAY) {
        selected = spaces.map(space => space.id);
        isAllowClick = false;
    } else { // Reset to old select
        isAllowClick = true;
        selected = selectedSpaces;
    }

    const toggle = (id) => {
        if (isAllowClick) {
            const newSelected = [id];
            if (onChange && newSelected[0] !== selected[0]) {
                onChange(newSelected)
            } else {
                onChange([])
            }
        }
    }

    return <div className={`select-space d-flex flex-column ${isAllowClick ? '' : 'cursor-not-allowed'}`}>
        {
            spaces.map(space => (
                <Button
                    active={selected.includes(space.id)}
                    key={space.id}
                    className={`mb-2 ${isAllowClick ? '' : 'not-allow-select'}`}
                    color="primary"
                    onClick={() => toggle(space.id)}
                    title={space.name}
                >
                    {space.name}
                </Button>
            ))
        }
    </div>
}
