import React from "react";
import PropTypes from "prop-types";
import { Calendar } from "@fullcalendar/core";
import { settingCalendarUIWithDayView } from "../../../helpers/UtilsHelper";
import constant from "../../../constants/index";

import { Button } from "reactstrap";

let viewMode = "";
class Navigator extends React.Component {
  next = () => {
    this.props.calendar.next();
    this.props.onChange();
  };

  prev = () => {
    this.props.calendar.prev();
    this.props.onChange();
  };

  changeView = (newView) => {
    if (newView !== viewMode) {
      viewMode = newView;
      const { calendar } = this.props;
      calendar.changeView(viewMode);
      this.props.onChange();

      if (newView === constant.CALENDAR_VIEW_TYPE.DAY) {
        settingCalendarUIWithDayView(this.props.spaces, this.props.calendar);
      }
    }
  };

  render() {
    return (
      <>
        {/* <Button
          className="fullcalendar-btn-prev btn-neutral"
          color="default"
          onClick={this.prev}
          size="sm"
        >
          <i className="fas fa-angle-left" />
        </Button>
        <Button
          className="fullcalendar-btn-next btn-neutral"
          color="default"
          onClick={this.next}
          size="sm"
        >
          <i className="fas fa-angle-right" />
        </Button> */}
        <Button
          className="btn-neutral"
          color="default"
          data-calendar-view="month"
          onClick={() => this.changeView(constant.CALENDAR_VIEW_TYPE.MONTH)}
          size="sm"
        >
          Month
        </Button>
        <Button
          className="btn-neutral"
          color="default"
          data-calendar-view="basicWeek"
          onClick={() => this.changeView(constant.CALENDAR_VIEW_TYPE.WEEK)}
          size="sm"
        >
          Week
        </Button>
        <Button
          className="btn-neutral"
          color="default"
          data-calendar-view="basicDay"
          onClick={() => this.changeView(constant.CALENDAR_VIEW_TYPE.DAY)}
          size="sm"
        >
          Day
        </Button>
      </>
    );
  }
}

Navigator.protoTypes = {
  calendar: PropTypes.instanceOf(Calendar),
};

export default Navigator;
