import React from "react";
import ReactDom from "react-dom";
import { connect } from "react-redux";
import { Calendar } from "@fullcalendar/core";
import dayGridPlugin from "@fullcalendar/daygrid";
import timeGridPlugin from "@fullcalendar/timegrid";
import resourceTimeGridPlugin from "@fullcalendar/resource-timegrid";
import interaction from "@fullcalendar/interaction";
import ReactBSAlert from "react-bootstrap-sweetalert";
import { Card, CardBody, Container, Row, Col } from "reactstrap";

import { events } from "variables/general.jsx";
import Loading from "../../../components/Loading";
import Navigator from "./Navigator";
import CustomEvent from "./CustomEvent";
import BookingModal from "./BookingModal";

import {
  initData,
  showDetailBooking,
  onCalendarChange,
} from "../../../redux/actions/adminDashboardActions";
import {
  getStartOfMonth,
  getEndOfMonth,
  getMomentObject,
  getUserFullName,
  getTotalHoursOfBookings,
} from "helpers/UtilsHelper";
import bookingApi from "api/bookingApi";
import VenueAndSpaceSelection from "./VenueAndSpaceSelection";
import constant from "../../../constants/index";

import "./Dashboard.scss";
import Button from "reactstrap/lib/Button";
let calendar: Calendar;

class CalendarView extends React.Component {
  state = {
    events: events,
    alert: null,
  };

  calendar = null;

  isSlectedSpace(selectedSpaces) {
    return selectedSpaces && selectedSpaces.length > 0;
  }

  removeAllEvent() {
    const events = calendar.getEvents();
    events.forEach((e) => {
      e.remove();
    });
  }

  getBookingList() {
    const filters = {
      start_date: getStartOfMonth(),
      end_date: getEndOfMonth(),
      limit: 1000000,
    };
    bookingApi.list(filters).then((res) => {
      const eventList =
        res.result &&
        res.result.map((item) => {
          const { id, startTime, startDate, endTime } = item;
          return {
            id,
            title: "Booking",
            start: getMomentObject(`${startDate} ${startTime}`).format(),
            end: getMomentObject(`${startDate} ${endTime}`).format(),
            resourceId: "a",
            allDay: false,
            className: "bg-blue",
            description: "This is description",
          };
        });
      if (eventList) {
        this.setState({ events: eventList }, () => {});
      }
    });
  }

  componentDidMount() {
    this.props.initData(null, this.createCalendar());
  }

  componentWillReceiveProps(nextProps) {
    this.removeAllEvent();
    this.updateCalendarData(nextProps);
  }

  async updateCalendarData(nextProps) {
    const { bookings, selectedSpaces } = nextProps;
    // When is day view mected spaceode or has been selected space than render detail booking
    if (
      calendar.state.viewType === constant.CALENDAR_VIEW_TYPE.DAY ||
      this.isSlectedSpace(selectedSpaces)
    ) {
      bookings.forEach((item) => {
        let event = {
          id: item.id,
          start: item.startDate + "T" + item.startTime,
          end: item.startDate + "T" + item.endTime,
          title: getUserFullName(item.provider),
          type: "BOOKING",
          providerId: item.provider.id,
          provider: item.provider,
          space: item.space,
          spacePrice: item.spacePrice,
          resourceId: item.space.id,
          allDay: false,
          // className: "bg-blue",
          description: "This is description",
        };
        calendar.addEvent(event);
      });
    } else {
      // Render total hours booking mode
      const totalHoursBooking = getTotalHoursOfBookings(bookings);
      totalHoursBooking.forEach((item) => {
        let event = {
          id: item.id,
          start: item.startDate + "T07:00:00",
          end: item.startDate + "T09:00:00",
          title: item.totalHoursBooked + " hours",
          type: "BOOKING_TOTAL_HOURS",
          className: "bg-total-hours",
          allDay: false,
          displayEventTime: false,
        };
        calendar.addEvent(event);
      });
    }
  }

  changeCalendar = () => {
    this.setState({
      currentDate: calendar.view.title,
    });

    this.props.onCalendarChange();
  };

  createCalendar = () => {
    calendar = new Calendar(this.refs.calendar, {
      plugins: [
        interaction,
        dayGridPlugin,
        timeGridPlugin,
        resourceTimeGridPlugin,
      ],
      defaultView: constant.CALENDAR_VIEW_TYPE.MONTH,
      selectable: true,
      selectHelper: true,
      allDaySlot: false,
      // slotDuration: "00:30",
      eventOverlap: false,
      // editable: true,
      schedulerLicenseKey: "0127232346-fcs-1571929973",
      // Edit calendar event action
      minTime: "06:00:00",

      eventLimit: true,
      eventClick: ({ event }) => {
        if (event.extendedProps.type === "BOOKING") {
          this.props.showDetailBooking(event);
        }
      },
      eventRender: function (info) {
        const type = info.event._def.extendedProps.type;
        if (type === "BOOKING") {
          ReactDom.render(<CustomEvent {...info} />, info.el);
        }
      },
    });
    calendar.render();
    this.calendar = calendar;
    this.setState({
      currentDate: calendar.view.title,
    });
    return calendar;
  };

  addNewEvent = () => {
    var newEvents = this.state.events;
    newEvents.push({
      title: this.state.eventTitle,
      start: this.state.startDate,
      end: this.state.endDate,
      className: this.state.radios,
      id: this.state.events[this.state.events.length - 1] + 1,
    });
    calendar.addEvent({
      title: this.state.eventTitle,
      start: this.state.startDate,
      end: this.state.endDate,
      className: this.state.radios,
      id: this.state.events[this.state.events.length - 1] + 1,
    });
    this.setState({
      modalAdd: false,
      events: newEvents,
      startDate: undefined,
      endDate: undefined,
      radios: "bg-info",
      eventTitle: undefined,
    });
  };
  changeEvent = () => {
    var newEvents = this.state.events.map((prop, key) => {
      if (prop.id + "" === this.state.eventId + "") {
        this.state.event.remove();
        calendar.addEvent({
          ...prop,
          title: this.state.eventTitle,
          className: this.state.radios,
          description: this.state.eventDescription,
        });
        return {
          ...prop,
          title: this.state.eventTitle,
          className: this.state.radios,
          description: this.state.eventDescription,
        };
      } else {
        return prop;
      }
    });
    this.setState({
      modalChange: false,
      events: newEvents,
      radios: "bg-info",
      eventTitle: undefined,
      eventDescription: undefined,
      eventId: undefined,
      event: undefined,
    });
  };
  deleteEventSweetAlert = () => {
    this.setState({
      alert: (
        <ReactBSAlert
          warning
          style={{ display: "block", marginTop: "-100px" }}
          title="Are you sure?"
          onConfirm={() =>
            this.setState({
              alert: false,
              radios: "bg-info",
              eventTitle: undefined,
              eventDescription: undefined,
              eventId: undefined,
            })
          }
          onCancel={() => this.deleteEvent()}
          confirmBtnCssClass="btn-secondary"
          cancelBtnBsStyle="danger"
          confirmBtnText="Cancel"
          cancelBtnText="Yes, delete it"
          showCancel
          btnSize=""
        >
          You won't be able to revert this!
        </ReactBSAlert>
      ),
    });
  };
  deleteEvent = () => {
    var newEvents = this.state.events.filter(
      (prop) => prop.id + "" !== this.state.eventId
    );
    this.state.event.remove();
    this.setState({
      alert: (
        <ReactBSAlert
          success
          style={{ display: "block", marginTop: "-100px" }}
          title="Success"
          onConfirm={() => this.setState({ alert: null })}
          onCancel={() => this.setState({ alert: null })}
          confirmBtnBsStyle="primary"
          confirmBtnText="Ok"
          btnSize=""
        >
          A few words about this sweet alert ...
        </ReactBSAlert>
      ),
      modalChange: false,
      events: newEvents,
      radios: "bg-info",
      eventTitle: undefined,
      eventDescription: undefined,
      eventId: undefined,
      event: undefined,
    });
  };

  next = () => {
    calendar.next();
    this.changeCalendar();
  };

  prev = () => {
    calendar.prev();
    this.changeCalendar();
  };

  render() {
    const { loading, spaces } = this.props;

    return (
      <div className="provider-dashboard">
        {this.state.alert}
        <div className="header header-dark bg-info pb-6 content__title content__title--calendar">
          <Container fluid>
            <div className="header-body">
              <Row className="align-items-center py-4">
                <Col lg="6">
                  <Button
                    className="fullcalendar-btn-prev btn-neutral"
                    color="default"
                    onClick={this.prev}
                    size="sm"
                  >
                    <i className="fas fa-angle-left" />
                  </Button>

                  <h6 className="fullcalendar-title h2 text-white d-inline-block mb-0 mr-1">
                    {this.state.currentDate}
                  </h6>
                  <Button
                    className="fullcalendar-btn-next btn-neutral"
                    color="default"
                    onClick={this.next}
                    size="sm"
                  >
                    <i className="fas fa-angle-right" />
                  </Button>
                </Col>
                <Col className="mt-3 mt-md-0 text-md-right" lg="6">
                  <Navigator
                    calendar={calendar}
                    spaces={spaces}
                    onChange={this.changeCalendar}
                  />
                </Col>
              </Row>
            </div>
          </Container>
        </div>
        <Container className="mt--6" fluid>
          {loading && <Loading />}
          <Row>
            <Col className="order-2 order-md-1">
              <Card className="card-calendar">
                <CardBody className="p-0">
                  <div
                    className="calendar"
                    data-toggle="calendar"
                    id="calendar"
                    ref="calendar"
                  />
                </CardBody>
              </Card>
            </Col>
            <Col md={2} className="order-1 order-md-2 pl-md-0">
              <VenueAndSpaceSelection />
            </Col>
          </Row>
        </Container>
        <BookingModal bookings={this.props.bookings} />
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    bookings: state.adminDashboard.bookings,
    loading: state.adminDashboard.loading,
    spaces: state.adminDashboard.spaces,
    selectedSpaces: state.adminDashboard.selectedSpaces,
    calendarViewType: state.adminDashboard.calendarViewType,
  };
};

const mapDispatchToProps = { initData, showDetailBooking, onCalendarChange };

export default connect(mapStateToProps, mapDispatchToProps)(CalendarView);
