import React from "react";
import ReactDom from "react-dom";
import { connect } from "react-redux";
import ReactDatetime from "react-datetime";
import Select2 from "react-select2-wrapper";
import moment from "moment";
import {
  Modal,
  Form,
  FormGroup,
  Button,
  ButtonGroup,
  Row,
  Col,
  Input,
} from "reactstrap";
import { toast } from "react-toastify";
import cons from "../../../constants";

import { getUserFullName, getDurationTime } from "../../../helpers/UtilsHelper";
import {
  closeEditBookingModal,
  cancelBooking,
  deleteBooking,
  updateBooking,
} from "../../../redux/actions/adminDashboardActions";
import Loading from "../../../components/Loading";

import ConfirmDeleteModal from "../../../components/Modal/ConfirmDeleteModal.jsx";
import SectionHeader from "../../../components/Form/SectionHeader";
import providerApi from "../../../api/providerApi";

import "./BookingModal.scss";

const defaultState = () => {
  let today = moment().startOf("date");
  let tomorrow = today.clone().add(1, "d");

  return {
    clientType: 1,
    venueType: 1,
    startDate: tomorrow,
    startTime: moment("8:00:00", "hh:mm:ss"),
    endTime: moment("9:00:00", "hh:mm:ss"),
    duration: 0,
    serviceType: "",
    unitPrice: 0,
    spacePrice: 0,
    comment: "",
    spaceId: "",
    firstName: null,
    lastName: null,
    email: null,
    phoneNumber: null,
    tier: 1,
    errors: {},
    isCancelBooking: false,
    isDeleteBooking: false,
    isChangeRoom: false,
    provider: null,
  };
};

class BookingModal extends React.Component {
  constructor(props) {
    super(props);
    this.formatSelectUser = this.formatSelectUser.bind(this);
    this.state = defaultState();
  }
  cancel = (e) => {
    e.preventDefault();
  };

  formatSelectUser = (user) => {
    const element = document.createElement("div");
    element.classList.add("select2-user");
    ReactDom.render(Select2User(user), element);
    return element;
  };

  validDate = (current) => {
    var yesterday = moment().subtract(1, "day");
    return current.isAfter(yesterday);
  };

  handleChange = (e) => {
    const { name, value } = e.target;
    delete this.state.errors[name];
    this.setState({ [name]: value });
  };

  onChangeState = (name, value) => {
    try {
      this.setState({ [name]: value });
    } catch (error) {}
  };

  onChangeRoom = (value) => {
    if (value) {
      this.settingPriceBySpaceId(value);
      // Check if the room changed
      const isChangeRoom =
        value !== this.props.selectedSpaces[0] ? true : false;
      this.setState({ isChangeRoom });
    }
  };

  settingPriceBySpaceId = (spaceId, newTier) => {
    const selectedRoom = this.props.spaces.filter(
      (item) => item.id === spaceId
    )[0];
    let tier = newTier ? newTier : this.state.tier;

    // Check have price, fix bug not prices
    let unitPrice = 0;
    if (selectedRoom.prices) {
      unitPrice = Number(selectedRoom.prices.find((p) => p.tier === tier).cost);
    }
    try {
      this.setState({ spaceId: spaceId, unitPrice, tier });
    } catch (error) {}
  };

  resetState = () => {
    this.setState(defaultState());
  };

  onCloseEditBookingModal = () => {
    this.resetState();
    this.props.closeEditBookingModal();
  };

  toggleConfirmDeleteBooking = () => {
    this.setState({ isDeleteBooking: !this.state.isDeleteBooking });
  };

  toggleConfirmCancelBooking = () => {
    this.setState({ isCancelBooking: !this.state.isCancelBooking });
  };

  cancelBooking = async () => {
    const providerId = this.props.currentBooking.providerId;
    const providerData = await providerApi
      .detail(providerId)
      .then((response) => {
        return response;
      });

    const countCancelationBalance = providerData.cancelationBalance;
    if (countCancelationBalance < 1) {
      toast.error("No more cancellations remaining.");
      this.toggleConfirmCancelBooking();
    } else {
      const bookingId = this.props.currentBooking.id;
      const status = cons.BOOKING_STATUS.CANCELED;
      const data = { status };
      this.toggleConfirmCancelBooking();
      this.props.cancelBooking(bookingId, data, this.props.bookings);
    }
  };

  deleteBooking = () => {
    const id = this.props.currentBooking.id;
    this.props.deleteBooking(id, this.props.bookings);
    this.toggleConfirmDeleteBooking();
  };

  updateBooking = () => {
    if (this.state.isChangeRoom) {
      const bookingId = this.props.currentBooking.id;
      let data = {
        space: this.state.spaceId,
      };
      this.props.updateBooking(bookingId, data, this.props.bookings);
    }
  };

  shouldComponentUpdate(nextProps, nextState) {
    if (!this.props.isShowEditBooking && nextProps.isShowEditBooking) {
      const { currentBooking } = nextProps;

      const startTime = moment(currentBooking.endTime);
      const endTime = moment(currentBooking.startTime);
      const duration = getDurationTime(startTime, endTime, "minutes") / 60;

      const venueType = currentBooking.space ? 1 : 2;
      const spaceId = currentBooking.space ? currentBooking.space.id : "";
      const spacePrice = currentBooking.spacePrice;
      const provider = currentBooking.provider;

      this.setState({
        startTime,
        endTime,
        duration,
        startDate: startTime,
        venueType,
        spaceId,
        spacePrice,
        provider,
      });
    }

    if (!this.props.isGetedTier && nextProps.isGetedTier) {
      this.settingPriceBySpaceId(this.state.spaceId, nextProps.tier);
    }

    return true;
  }

  render() {
    const { isShowEditBooking, venues, spaces, selectedVenue, loading } =
      this.props;

    let {
      startDate,
      duration,
      startTime,
      endTime,
      spaceId,
      unitPrice,
      spacePrice,
      isChangeRoom,
      provider,
    } = this.state;

    spaces.forEach((space) => (space.text = space.name));

    const venue = venues.find((venue) => venue.id === selectedVenue);
    const space = spaces.find((space) => space.id === spaceId);

    return (
      <Modal isOpen={isShowEditBooking} size="lg" className="row booking-modal">
        <ConfirmDeleteModal
          showTitle={false}
          className="modal-dialog-centered"
          isOpen={this.state.isDeleteBooking}
          toggle={this.toggleConfirmDeleteBooking}
          handle={this.deleteBooking}
          content={`Do you want to delete this booking?`}
        />
        <ConfirmDeleteModal
          showTitle={false}
          className="modal-dialog-centered"
          isOpen={this.state.isCancelBooking}
          toggle={this.toggleConfirmCancelBooking}
          handle={this.cancelBooking}
          content={`Are you sure you want to cancel this booking?`}
        />
        <Row className="mr-0 ml-0">
          <Col md={3} className="preview">
            <button
              type="button"
              className="close d-flex "
              aria-label="Close"
              onClick={this.onCloseEditBookingModal}
            >
              <span aria-hidden="true">&times;</span>
            </button>
            <div className="time d-flex flex-column">
              <span className="font-weight-bold">
                {startDate.calendar().split(" at ")[0]}
              </span>
              <span className="font-weight-light">
                {startTime.format("HH:mm")} to {endTime.format("HH:mm")}
              </span>
            </div>
            <div className="duration d-flex flex-column mt-4">
              <span className="value">{duration}</span>
              <span className="label color-light">HOURS</span>
            </div>
            {provider && (
              <div className="client d-flex flex-column mt-4">
                <label className="label-highlight">Provider</label>
                <div className="user">
                  <img
                    alt="..."
                    className="avatar avatar-sm rounded-circle mr-2"
                    src={
                      !!provider.avatar
                        ? provider.avatar
                        : require("assets/img/theme/default-avatar.png")
                    }
                  />
                  <span>{getUserFullName(provider)}</span>
                </div>
              </div>
            )}
            {space && (
              <div className="space d-flex flex-column mt-4">
                <label className="label-highlight">Space</label>
                {!!space.images.length && (
                  <img alt="..." src={space.images[0].file} />
                )}
                <span className="mt-1">{space.name}</span>
                <span className="color-light">At venue {venue.name}</span>
              </div>
            )}
            <div className="space-price d-flex flex-column mt-4">
              <label className="label-highlight mb-0">Booking total</label>
              <div className="money">
                <span className="color-light">$</span>
                <span className="value">
                  {isChangeRoom ? unitPrice * duration : Number(spacePrice)}
                </span>
              </div>
            </div>
          </Col>
          <Col md={9} className="detail">
            {loading && <Loading />}
            <div className="header">
              <i className="fa fa-book-open"></i>
              <h2>Edit booking</h2>
            </div>
            <Form>
              <div className="date-time">
                <SectionHeader icon="far fa-clock" title="Date and time" />
                <Row>
                  <Col md={6}>
                    <FormGroup>
                      <label
                        className="form-control-label"
                        htmlFor="exampleDatepicker"
                      >
                        Date
                      </label>
                      <ReactDatetime
                        timeFormat={false}
                        isValidDate={this.validDate}
                        value={startDate}
                        inputProps={{ disabled: true }}
                        closeOnSelect={true}
                      >
                        <span className="btn-inner--icon">
                          <i className="ni ni-atom" />
                        </span>
                      </ReactDatetime>
                    </FormGroup>
                  </Col>
                  <Col md={6}></Col>
                  <Col md={3}>
                    <FormGroup>
                      <label
                        className="form-control-label"
                        htmlFor="exampleDatepicker"
                      >
                        Start time
                      </label>
                      <ReactDatetime
                        value={startTime}
                        timeFormat={true}
                        dateFormat={false}
                        timeConstraints={{ minutes: { step: 15 } }}
                        inputProps={{ disabled: true }}
                        closeOnSelect={true}
                      />
                    </FormGroup>
                  </Col>
                  <Col md={3}>
                    <FormGroup>
                      <label
                        className="form-control-label"
                        htmlFor="exampleDatepicker"
                      >
                        Duration
                      </label>
                      <Input
                        type="number"
                        step="0.25"
                        min="1"
                        max="14"
                        value={duration}
                        disabled={true}
                      />
                    </FormGroup>
                  </Col>
                  <Col md={3}>
                    <FormGroup>
                      <label
                        className="form-control-label"
                        htmlFor="exampleDatepicker"
                      >
                        End time
                      </label>
                      <ReactDatetime
                        value={endTime}
                        timeFormat={true}
                        dateFormat={false}
                        timeConstraints={{ minutes: { step: 15 } }}
                        inputProps={{ disabled: true }}
                        closeOnSelect={true}
                      />
                    </FormGroup>
                  </Col>
                </Row>
              </div>
              <div className="venue mb-4">
                <SectionHeader icon="far fa-building" title="Venue" />
                <Row>
                  <Col>
                    <ButtonGroup className="mt-2">
                      <Button
                        color="primary"
                        type="button"
                        active={this.state.venueType === 1}
                      >
                        <i className="fa fa-circle"></i>Ethera venue
                      </Button>
                      <Button
                        color="primary"
                        type="button"
                        active={this.state.venueType === 2}
                      >
                        <i className="fa fa-circle"></i>Alternate venue
                      </Button>
                    </ButtonGroup>
                  </Col>
                </Row>
                {this.state.venueType === 1 && (
                  <Row>
                    <Col md={6} className="mt-2">
                      <FormGroup className="mb-0">
                        <label
                          className="form-control-label"
                          htmlFor="exampleDatepicker"
                        >
                          Room
                        </label>
                        <Select2
                          className="form-control"
                          defaultValue="0"
                          options={{
                            placeholder: "Select",
                            // templateResult: state => state.id ? state.name : state.text,
                            // templateSelection: state => state.id ? state.name : state.text,
                            data: spaces,
                          }}
                          value={spaceId}
                          onChange={(e) => this.onChangeRoom(e.target.value)}
                        />
                      </FormGroup>
                    </Col>
                  </Row>
                )}
              </div>
              <div className="d-flex justify-content-between">
                <Button
                  color="link"
                  className="pl-0"
                  onClick={this.onCloseEditBookingModal}
                >
                  Close
                </Button>
                <Button
                  color="default"
                  onClick={this.toggleConfirmCancelBooking}
                >
                  Cancel
                </Button>
                <Button
                  color="danger"
                  onClick={this.toggleConfirmDeleteBooking}
                >
                  Delete
                </Button>
                <Button
                  color="success"
                  onClick={this.updateBooking}
                  disabled={!isChangeRoom}
                >
                  Save
                </Button>
              </div>
            </Form>
          </Col>
        </Row>
      </Modal>
    );
  }
}

function Select2User(user) {
  let label = user.id === "" ? user.text : getUserFullName(user);
  return (
    <>
      <img
        alt="..."
        className="avatar avatar-sm rounded-circle mr-2"
        src={
          user.avatar
            ? user.avatar
            : require("assets/img/theme/default-avatar.png")
        }
      />
      <span>{label}</span>
    </>
  );
}

const mapStateToProps = (state) => {
  const {
    isShowEditBooking,
    currentBooking,
    clients,
    venues,
    spaces,
    selectedVenue,
    loading,
    selectedSpaces,
    tier,
    isGetedTier,
  } = state.adminDashboard;

  return {
    isShowEditBooking,
    currentBooking,
    venues,
    clients,
    spaces,
    selectedVenue,
    loading,
    selectedSpaces,
    tier,
    isGetedTier,
  };
};

const mapActionsToProps = {
  closeEditBookingModal,
  cancelBooking,
  deleteBooking,
  updateBooking,
};

export default connect(mapStateToProps, mapActionsToProps)(BookingModal);
